import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { Root, View, Panel, PanelHeader } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import { RouteNode } from 'react-router5'

import ViewItem from './components/ViewItem';
import HomePage from './components/HomePage';

import './css/main.css';
class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			items: [
				{
					id: 1,
					name: 'Burger King',
					cat_name: 'Ресторан',
					description: 'Show me the Persik, please Show me the Persik, please Show me the Persik, please Show me the Persik, please'
				},
				{
					id: 2,
					name: 'Burger King',
					cat_name: 'Ресторан',
					description: 'Show me the Persik, please Show me the Persik, please Show me the Persik, please Show me the Persik, please'
				},
				{
					id: 3,
					name: 'Burger King',
					cat_name: 'Ресторан',
					description: 'Show me the Persik, please Show me the Persik, please Show me the Persik, please Show me the Persik, please'
				},
				{
					id: 4,
					name: 'Burger King',
					cat_name: 'Ресторан',
					description: 'Show me the Persik, please Show me the Persik, please Show me the Persik, please Show me the Persik, please'
				},
				{
					id: 5,
					name: 'Burger King',
					cat_name: 'Ресторан',
					description: 'Show me the Persik, please Show me the Persik, please Show me the Persik, please Show me the Persik, please'
				}
			],
			categories: [
				{
					id: 1,
					name: 'Ресторан',
				},
				{
					id: 2,
					name: 'Бар',
				},
				{
					id: 3,
					name: 'Фастфуд',
				},
				{
					id: 4,
					name: 'Суши',
				}
			],
			recommendations: [
				{
					id: 1,
					name: 'Burger King'
				},
				{
					id: 2,
					name: 'Шоколадница'
				},
				{
					id: 3,
					name: 'МуМу'
				}
			],
			activePanel: 'home',
			currentTaskId : null,
			fetchedUser: null,
			item_id: null,
		};
	}
	
	//Функция для добавления скидки
	addItem = (item) => {
		item.id = this.state.items.length + 1;
		this.setState({
			items: [...this.state.items, item]
		})
	}
	//Текущая скидка
	SetitemCurrentId = (currentTaskId) => this.setState({ currentTaskId })

	get item_id () {
		const id = Number(this.props.route.params.id) || this.state.currentTaskId
		return this.state.items.filter((item) =>
			item.id === id
		)
	}

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				case 'VKWebAppSetViewSettingsFailed':
					console.log(e.detail.data);
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
		connect.send("VKWebAppSetViewSettings", {"status_bar_style": "light", "action_bar_color": "#19082c"});
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	render() {
		let {
			route,
			router
		} = this.props;

		const activeView = (route.name === 'add') ? 'addView' : 'itemsView';
		const activePanel = route.name;

		return (
			<Root activeView={activeView}>
				<View activePanel={activePanel} id='itemsView'>
					<Panel id='home'>
					<HomePage 
							router={router}
							categories={this.state.categories}
							items={this.state.items}
							SetitemCurrentId={this.SetitemCurrentId}
							recommends={this.state.recommendations}
						/>
					</Panel>
					<Panel id='cat'>
						<PanelHeader>Test OnClickCat</PanelHeader>
						Test
					</Panel>
					<Panel id='item'>
						<ViewItem 
							router={router}
							item={this.item_id[0]}
						/>
					</Panel>
				</View>
			</Root>
		);
	}
}

export default (props) => (
    <RouteNode nodeName="">
        {({ route }) => <App route={route} {...props}/>}
    </RouteNode>
)
