import React from 'react'
import { Div, PanelHeader, HorizontalScroll, platform, ANDROID } from '@vkontakte/vkui'

import '../css/header.css';
import '../css/places.css';
import '../css/cat_item.css';
import '../css/block_item.css';

import '@vkontakte/vkui/dist/vkui.css'

//Для теста
import persik from '../img/persik.png';

import TextBlock from './TextBlock';

import Icon28Notifications from '@vkontakte/icons/dist/28/notifications';

const itemStyle = {
	flexShrink: 0,
	display: 'flex'
};

class HomePage extends React.Component {
    render() {
        let {
            items,
            SetitemCurrentId,
            categories,
            router,
            recommends,
            userInfo
        } = this.props;

        const osname = platform()

        return (
            <div>
                <PanelHeader noShadow>Havcheek</PanelHeader>
                <Div className="Header">
                    <Div className="InfoHeader">
                        <Div className="InfoUser">
                            <img className="HeaderImg" src={persik} alt="Avatar"/>
                            <div className="InfoImg">
                                <div className="TextBold">Vladimir Starodubtsev</div>
                                <div className="TextSmall">Moscow</div>
                            </div>
                        </Div>
                        <div className="InfoNotice"><Icon28Notifications /></div>
                    </Div>
                    <HorizontalScroll className="HorScroll">
                        <div style={{ display: 'flex' }}>
                            {
                                categories.map((cat, index) => (
                                    <div className="CatBlock" style={itemStyle} onClick={()=> {
                                            router.navigate('cat', { id : cat.id })
                                            } 
                                        }>
                                        <div className="CatItemImg"></div>
                                        <div className="CatText">
                                            <div className="CatItemText">{cat.name}</div>
                                            <div className="CatItemTextDesc">Посмотреть</div>
                                        </div>
                                    </div>
                                ))   
                            }
                        </div>
                    </HorizontalScroll>
                </Div>
                <Div className="Container">
                    <TextBlock title='Рекомендации' description='Рекомендованные места'/>

                    {
                        recommends.map((rec, index) => (
                            <div className="PlaceBlock" onClick={() => {
                                router.navigate('place', {id : rec.id})
                            }}>
                                <div className="PlaceItemImg"></div>
                                <div className="PlaceText">
                                    <div className="PlaceItemText">{rec.name}</div>
                                    <div className="PlaceItemTextDesc">Показать все скидки и акции</div>
                                </div>
                            </div>
                            
                        ))
                    }
                    <div className="MarginTop">
                        <TextBlock title='Популярное' description='Популярные скидки и купоны'/>

                            {
                                items.map((item, index) => (
                                    <div className='BlockItem'onClick={() => {
                                        router.navigate('item', {id : item.id})
                                    }}>
                                        <div className="BlockFavorite"><Icon28Notifications /></div>
                                        <div className='BlockImage'><img className="BlockItemImage" src={persik} alt="BlockItemImage"/></div>
                                        <div className='BlockInfo'>
                                            <div className='BlockTitle'>{item.name}</div>
                                            <div className='BlockCategory'>{item.cat_name}</div>
                                            <div className='BlockDescp'>{item.description.length > 60 ? item.description.substring(0, 60 - 3) + '...' : item.description}</div>
                                        </div>
                                    </div>
                                ))
                            }

                    </div>
                    
                    <TextBlock title='Последнее' description='Последние скидки и акции'/>
                </Div>
            </div>
        );
    }
} 
